






















import {Component, Prop, Vue} from "vue-property-decorator"

@Component
export default class Panel extends Vue {
  @Prop(Number) expression!: number
  output: string = this.expression.toString()

  typeNumber(e: MouseEvent) {
    const button = (e.target as HTMLButtonElement)
    const input = button.textContent as string
    if (this.output.length === 16) {
      return
    }
    if (this.output === "0") {
      if ("0123456789".indexOf(input) >= 0) {
        this.output = input
      } else {
        this.output += input
      }
      return
    }
    if (this.output.indexOf(".") >= 0 && input === ".") {
      return
    }
    this.output += input
  }

  deleteNumber() {
    const length = this.output.length
    this.output = this.output.substr(0, length - 1)
    if (length === 1) {
      this.output = "0"
    }
  }

  clearNumber() {
    this.output = "0"
  }

  calculateNumber() {
    const res = parseFloat(this.output)
    this.$emit("update:expression", res)
    this.$emit("submit")
    this.output = "0"
  }
}
